/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import ServerSideRender from '@wordpress/server-side-render';
import { useState } from '@wordpress/element';
import { InspectorControls, BlockControls, useBlockProps, URLInput } from '@wordpress/block-editor';
import {
	PanelBody,
	TextControl,
	BaseControl,
	RadioControl,
	SelectControl,
	ButtonGroup,
	Button,
	RangeControl,
} from '@wordpress/components';

/**
 * @Internal dependencies
 */
import icon from './icon';
import metadata from './block.json';
import { ArkbMarginControl } from '@components';

/**
 * 設定項目
 */
const targetOptions = [
	{
		label: __('Child Pages of the current page', 'arkhe-blocks'),
		value: 'children',
	},
	{
		label: __('Child Pages of specified page', 'arkhe-blocks'),
		value: 'specified-parent',
	},
	{
		label: __('Specify by ID', 'arkhe-blocks'),
		value: 'id',
	},
];

const hTags = [
	{
		label: 'h2',
		val: 'h2',
	},
	{
		label: 'h3',
		val: 'h3',
	},
	{
		label: 'div',
		val: 'div',
	},
];

// 何を基準に並べるか
const orderbyOptions = [
	{
		label: __('"Order" setting', 'arkhe-blocks'),
		value: 'menu_order',
	},
	{
		label: __('Release date', 'arkhe-blocks'),
		value: 'date',
	},
	{
		label: __('Random', 'arkhe-blocks'),
		value: 'rand',
	},
];

const orderBtns = [
	{
		label: __('Descending order', 'arkhe-blocks'),
		value: 'DESC',
	},
	{
		label: __('Ascending order', 'arkhe-blocks'),
		value: 'ASC',
	},
];

/**
 * 設定
 */
const listTypeOptions = [
	{
		label: __('Card type', 'arkhe-blocks'),
		value: 'card',
	},
	{
		label: __('List type', 'arkhe-blocks'),
		value: 'list',
	},
	{
		label: __('Text type', 'arkhe-blocks'),
		value: 'simple',
	},
];

/**
 * Register Block
 */
const blockName = 'ark-block-pageList';
registerBlockType(metadata.name, {
	title: __('Page list', 'arkhe-blocks'),
	description: __('Create a page list with the specified conditions.', 'arkhe-blocks'),
	icon,
	edit: ({ attributes, setAttributes }) => {
		const { listType, target, postID, parentID, hTag, excerptLength, orderby, order } =
			attributes;

		const [searchText, setSearchText] = useState('');

		const blockProps = useBlockProps({
			className: blockName,
		});

		return (
			<>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody
						title={__('Pages to display', 'arkhe-blocks')}
						initialOpen={true}
						className='arkb-panel--pageList'
					>
						<SelectControl
							value={target}
							options={targetOptions}
							onChange={(val) => {
								setAttributes({ target: val });
							}}
						/>
						{'id' === target && (
							<TextControl
								className='arkb-control--pageid'
								label='IDs'
								placeholder='ex) 8,120,272'
								help={`※ ${__(
									'If there are multiple, enter them separated by ",".',
									'arkhe-blocks'
								)}`}
								value={postID || ''}
								onChange={(value) => {
									setAttributes({ postID: value });
								}}
							/>
						)}
						{'specified-parent' === target && (
							<>
								<TextControl
									label='ID'
									className='arkb-control--pageid'
									placeholder={__(
										'Enter the ID of the parent page',
										'arkhe-blocks'
									)}
									type='number'
									min={1}
									value={parentID || ''}
									onChange={(value) => {
										const newID = value ? parseInt(value) : undefined;
										setAttributes({ parentID: newID });
									}}
								/>
								<div className='arkb-control--searchPage'>
									<URLInput
										value={searchText}
										placeholder={__('Search by Page name', 'arkhe-blocks')}
										onChange={(url, post) => {
											const seletedID = post?.id;
											if (seletedID) {
												setSearchText('');
												setAttributes({ parentID: seletedID });
											} else {
												setSearchText(url);
											}
										}}
									/>
								</div>
							</>
						)}
					</PanelBody>
					<PanelBody
						title={__('Display settings', 'arkhe-blocks')}
						initialOpen={true}
						className='arkb-panel--pageList'
					>
						<RadioControl
							label={__('List layout', 'arkhe-blocks')}
							selected={listType}
							options={listTypeOptions}
							onChange={(val) => {
								setAttributes({ listType: val });
							}}
						/>
						<RangeControl
							label={__('Number of characters in the excerpt', 'arkhe-blocks')}
							help={__(
								'If "Excerpt" is entered, its contents will be displayed.',
								'arkhe-blocks'
							)}
							value={excerptLength}
							onChange={(val) => {
								setAttributes({ excerptLength: val });
							}}
							min={0}
							max={240}
							allowReset={true}
							className='arkb-range--useReset'
						/>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('HTML tag for title', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ButtonGroup>
								{hTags.map((btn) => {
									const isSlected = btn.val === hTag;
									return (
										<Button
											// isSecondary={ ! isSlected }
											isPrimary={isSlected}
											onClick={() => {
												setAttributes({ hTag: btn.val });
											}}
											key={`hTag_${btn.val}`}
										>
											{btn.label}
										</Button>
									);
								})}
							</ButtonGroup>
						</BaseControl>
					</PanelBody>
					<PanelBody
						title={__('Sorting order setting', 'arkhe-blocks')}
						initialOpen={true}
					>
						<RadioControl
							label={__('What to arrange based on', 'arkhe-blocks')} //'何を基準に並べるか'
							selected={orderby}
							options={orderbyOptions}
							onChange={(val) => {
								setAttributes({ orderby: val });
							}}
						/>
						{/* '降順か昇順か' */}
						<ButtonGroup>
							{orderBtns.map((btn) => {
								return (
									<Button
										isPrimary={btn.value === order}
										onClick={() => {
											setAttributes({ order: btn.value });
										}}
										key={`order_${btn.value}`}
									>
										{btn.label}
									</Button>
								);
							})}
						</ButtonGroup>
					</PanelBody>
				</InspectorControls>
				<div {...blockProps}>
					<ServerSideRender block={metadata.name} attributes={attributes} />
				</div>
			</>
		);
	},
	save: () => {
		return null;
	},
});
